import React from 'react';
import photoJulieBRUANT from "../Assets/Administrateurs/Bruant.jpg";
import photoAudreyPHILIPPE from "../Assets/Administrateurs/Audrey.jpg";
import photoJulieBRUNETTO from "../Assets/Administrateurs/Brunetto.jpg";
import photoBaptisteREDON from "../Assets/Administrateurs/Baptiste.jpg";
import photoPaulPETIT from "../Assets/Administrateurs/Paul.jpg";
import photoSarahHONORE from "../Assets/Administrateurs/Sarah.jpg";
import photoMorganeROY from "../Assets/Administrateurs/Morgane.jpg";
import photoAdrienCOULLOUMME from "../Assets/Administrateurs/Adrien.jpg";
import photoAudrenBUTERY from "../Assets/Administrateurs/Audren.jpg";
import './UaAdministrateur.css';

const photosAdministrateurs = {
    JulieBRUANT: photoJulieBRUANT,
    AudreyPHILIPPE: photoAudreyPHILIPPE,
    JulieBRUNETTO: photoJulieBRUNETTO,
    BaptisteREDON: photoBaptisteREDON,
    PaulPETIT: photoPaulPETIT,
    SarahHONORÉ: photoSarahHONORE,
    MorganeROY: photoMorganeROY,
    AdrienCOULLOUMME: photoAdrienCOULLOUMME,
    AudrenBUTERY: photoAudrenBUTERY
}

export default class UaAdministrateurDroite extends React.Component{
    constructor(props){
        super(props);
        this.PhotoAdministrateur = photosAdministrateurs[this.props.prenom + this.props.nom];
    }

    render() {
        return(
            <div className="UaAdministrateur-Container UaAdministrateurDroite-Container" id={"UaAdministrateur"+this.props.prenom+this.props.nom}>
                <div className={"UaAdministrateur-LosangePhotoPole"}>
                    <div id={"couleurPole1"+this.props.prenom+this.props.nom}/>
                    <img className={"photoAdministrateur"} src={this.PhotoAdministrateur} alt={"Photo d'un administrateur de l'UA"}/>
                    <div id={"couleurPole2"+this.props.prenom+this.props.nom}/>
                </div>
                <div className={"UaAdministrateurDroite-texte"}>
                    <h4 className={"UaAdministrateur-texte"}> {this.props.prenom + " " + this.props.nom} </h4>
                    <h5 className={"UaAdministrateur-texte"}> {this.props.poste} </h5>
                    <h6 className={"UaAdministrateur-texte"}> {this.props.pole} </h6>
                </div>
            </div>
        )
    }
}