import React from 'react';
import './AccueilAboutUs.css';
import './AccueilCAS.css'
import Fleche from "../Assets/LosangeCoupeDroite.png";
import CAS_photo from "../Assets/CAS.jpg";

export default class AccueilCAS extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <div  id="AccueilCAS-Container">
                <div id={"AccueilCAS-texte"}>
                    <div id={"AccueilCAS-titre"}>
                        <img alt="DecorationFLeche" id={"AccueilCAS-fleche"} src={Fleche}/>
                        <h3 id={"AccueilCAS-h3"}> CONSEIL <br/> ASSOCIATIF <br/> STRATÉGIQUE </h3>
                    </div>
                    <p className={"AccueilCAS-p"}>Toutes les décisions de l’Union des Associations sont discutées, évaluées et adoptées au cours du Conseil Associatif Stratégique (CAS). Ce conseil est constitué de l’ensemble des administrateurs de l’Union des Associations, d’un représentant moral par association et de membres d’honneur. </p>
                    <img id={"AccueilCAS-CAS"} src={CAS_photo} alt={"Photo d'un conseil associatif stratégique"} />
                </div>
            </div>
        )
    }
}