import React from 'react';
import "./Accueil.css";
import ImageDeGarde from "../Components/ImageDeGarde";
import AccueilAboutUs from "../Components/AccueilAboutUs";
import AccueilTeam from "../Components/AccueilTeam";
import AccueilCAS from "../Components/AccueilCAS";

export default class Accueil extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <div id="Accueil-Container">
                <div className={"Pages"}>
                    <div className={"LeftColumn"}/>
                    <div className={"MiddleColumn"} id={"Content_AccueilContainer"}>
                        <ImageDeGarde Page={"Accueil"}/>
                        <AccueilAboutUs/>
                        <AccueilTeam/>
                        <AccueilCAS/>
                    </div>
                    <div className={"RightColumn"}/>
                </div>
            </div>
        )
    }
}