import React from 'react';
import './AccueilTeam.css';
import Fleche from "../Assets/LosangeCoupeDroite.png";
import AccueilTeamOrganigramme from "./AccueilTeamOrganigramme";

export default class AccueilTeam extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <div  id="AccueilTeam-Container">
                <div id={"AccueilTeam-texte"}>
                    <div id={"AccueilTeam-titre"}>
                        <h3 id={"AccueilTeam-h3"}> L'ÉQUIPE </h3>
                        <img alt="DecorationFLeche" id={"AccueilTeam-fleche"} src={Fleche}/>
                    </div>
                    <p className={"AccueilTeam-p"}> Le Conseil d’Administration (CA) de l’association est composé d’une dizaine de membres. Chaque administrateur se voit attribuer un poste et un pôle associatif dont il sera le référent pour le reste du mandat. </p>
                    <p className={"AccueilTeam-p"}> La principale mission du CA est de préparer les dossiers relatifs aux interactions entre les associations et l’école mais aussi entres les associations elles-mêmes.</p>
                    <AccueilTeamOrganigramme/>
                </div>
            </div>
        )
    }
}