import React from 'react';
import './AccueilAboutUs.css';
import Fleche from "../Assets/LosangeCoupeDroite.png";

export default class AccueilAboutUs extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <div  id="AccueilAboutUs-Container">
                <div id={"AboutUs-texte"}>
                    <div id={"AboutUs-titre"}>
                        <img alt="DecorationFLeche" id={"AboutUs-fleche"} src={Fleche}/>
                        <h3 id={"AboutUs-h3"}> À PROPOS </h3>
                    </div>
                    <p className={"AboutUs-p Accueil-p"}> Avec près de 40 associations, la vie associative est impriné dans les gênes des étudiants de Centrale Marseille. La nécessité d’une fédération chargée de pérenniser et de représenter cette richesse a rapidement vu le jour : l’Union des Associations. </p>
                    <p className={"AboutUs-p Accueil-p"}> Interlocutrice privilégiée de la directeur de l’école, négociatrice de partenariats profitant à toutes les associations, accompagnatrice des nouvelles équipes associatives, l’Union des Associations est un pillier des activités des Centraliens Marseillais.</p>
                </div>
            </div>
        )
    }
}